import React, { useState } from "react";
import Flexbox from "commons/components/Flexbox";
import Counter from "./Counter";
import styled from "styled-components";
// import Modal from "commons/components/Modal";
// import Button from "commons/components/Button";
import CircleRenderer from "./CircleRenderer";
import { emissions as mockEmissions, EMISSION_LABELS, EMISSION_COLORS } from "data/mock-emissions";
import { absorptions as mockAbsorptions, ABSORPTION_LABELS, ABSORPTION_COLORS } from "data/mock-absorptions";
import PieChart from "./PieChart";
import SectionHeader from "./SectionHeader";
import Typography from "commons/components/Typography";
import city from "commons/images/city.png";
import city2 from "commons/images/city2.png";
import Logo from "commons/components/Logo";
import Comparison from "./Comparison";
import AnimatedSectionHeader from "./AnimatedSectionHeader";

const Box = styled(Flexbox)`
  position: relative;
`;

const Section = styled.section`
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 30%;
`;

const CityImage = styled.img`
  position: absolute;
  width: 100%;
  max-width: 600px;
  height: auto;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 45%);
  z-index: var(--z-index-below);
  /* background-image: url("./city.png"); */
  /* background-size: cover; */
  /* background-position: center; */
  object-fit: cover;
`;

const FooterImage = styled.div`
  width: calc(100% + 48px);
  height: 300px;
  margin-left: -24px;
  background-size: contain;
  background-position: center;
  background-attachment: fixed;
`;

function Home() {
  const [emissions, setEmissions] = useState(
    Object.entries(mockEmissions).map(([key, properties]) => ({
      label: EMISSION_LABELS[key],
      color: EMISSION_COLORS[key],
      value: properties.average,
      ...properties,
    }))
  );

  const [absorptions, setAbsorptions] = useState(
    Object.entries(mockAbsorptions).map(([key, properties]) => ({
      label: ABSORPTION_LABELS[key],
      color: ABSORPTION_COLORS[key],
      value: properties.average,
      ...properties,
    }))
  );

  const emissionsVsAbsorption = [
    {
      color: "#fc8b7f",
      factor: 1,
      label: "Emissions",
      value: 5900000,
    },
    {
      color: "#56b344",
      factor: 1,
      label: "Absorption",
      value: 36000,
    },
  ];

  return (
    <>
      <Logo />
      <Box flexDirection="column" paddingX={24}>
        <Section>
          <AnimatedSectionHeader>
            Since you&apos;ve entered this page, the city of Krakw has emitted <Counter /> kg of CO2.
          </AnimatedSectionHeader>
          <CityImage src={city} alt="Isometric city illustration" />
        </Section>

        <Section>
          <AnimatedSectionHeader>That&apos;s 5.9 million tonnes of CO2 annually.</AnimatedSectionHeader>
        </Section>

        <Section>
          <SectionHeader>Here&apos;s a breakdown of the major contributors to the city&apos;s emissions.</SectionHeader>
          <PieChart />
        </Section>

        <Section>
          <SectionHeader>How would changes in your daily life affect these emissions?</SectionHeader>
          <CircleRenderer circles={emissions} setCircles={setEmissions} maxValue={11550} showTitle />
        </Section>

        <Section>
          <SectionHeader>
            Let&apos;s see how many different kinds of trees you would need to offset emissions set above.
          </SectionHeader>
          <Comparison emissions={emissions} />
        </Section>

        <Section>
          <SectionHeader>The green spaces in Krakw help absorb some of our emissions.</SectionHeader>
          <CircleRenderer circles={absorptions} setCircles={setAbsorptions} maxValue={180000} />
        </Section>

        <Section>
          <SectionHeader>But how much is absorbed exactly?</SectionHeader>
          <CircleRenderer circles={emissionsVsAbsorption} maxValue={3000000} />
          <Typography variant="paragraph" marginTop={24}>
            Yes, that little green dot is the estimated absorption. As you can see, it&apos;s far from offseting the
            city&apos;s emissions.
          </Typography>
        </Section>

        <FooterImage style={{ backgroundImage: "url(" + city2 + ")" }} />
      </Box>
    </>
  );
}

export default Home;
